	module.exports = {"pathContext":{"docHtml":"<h1 align=\"center\">\n  <a href=\"http://chaijs.com\" title=\"Chai Documentation\">\n    <img alt=\"ChaiJS\" src=\"http://chaijs.com/img/chai-logo.png\">\n  </a>\n  <br>\n  chai\n</h1>\n<p align=center>\n  Chai is a BDD / TDD assertion library for <a href=\"http://nodejs.org\">node</a> and the browser that can be delightfully paired with any javascript testing framework.\n</p>\n<p align=\"center\">\n  <a href=\"./LICENSE\">\n    <img alt=\"license:mit\" src=\"https://img.shields.io/badge/license-mit-green.svg?style=flat-square\">\n  </a>\n  <a href=\"https://github.com/chaijs/chai/releases\">\n    <img alt=\"tag:?\" src=\"https://img.shields.io/github/tag/chaijs/chai.svg?style=flat-square\">\n  </a>\n  <a href=\"https://www.npmjs.com/packages/chai\">\n    <img alt=\"node:?\" src=\"https://img.shields.io/badge/node-%3E=4.0-blue.svg?style=flat-square\">\n  </a>\n  <br>\n  <a href=\"https://saucelabs.com/u/chaijs\">\n    <img alt=\"Selenium Test Status\" src=\"https://saucelabs.com/browser-matrix/chaijs.svg\">\n  </a>\n  <br>\n  <a href=\"https://www.npmjs.com/packages/chai\">\n    <img alt=\"downloads:?\" src=\"https://img.shields.io/npm/dm/chai.svg?style=flat-square\">\n  </a>\n  <a href=\"https://travis-ci.org/chaijs/chai\">\n    <img alt=\"build:?\" src=\"https://img.shields.io/travis/chaijs/chai/master.svg?style=flat-square\">\n  </a>\n  <a href=\"https://coveralls.io/r/chaijs/chai\">\n    <img alt=\"coverage:?\" src=\"https://img.shields.io/coveralls/chaijs/chai/master.svg?style=flat-square\">\n  </a>\n  <a href=\"\">\n    <img alt=\"devDependencies:?\" src=\"https://img.shields.io/david/chaijs/chai.svg?style=flat-square\">\n  </a>\n  <br>\n  <a href=\"https://chai-slack.herokuapp.com/\">\n    <img alt=\"Join the Slack chat\" src=\"https://img.shields.io/badge/slack-join%20chat-E2206F.svg?style=flat-square\">\n  </a>\n  <a href=\"https://gitter.im/chaijs/chai\">\n    <img alt=\"Join the Gitter chat\" src=\"https://img.shields.io/badge/gitter-join%20chat-D0104D.svg?style=flat-square\">\n  </a>\n  <a href=\"https://opencollective.com/chaijs\">\n    <img alt=\"OpenCollective Backers\" src=\"https://opencollective.com/chaijs/backers/badge.svg?style=flat-square\">\n  </a>\n</p>\n<p>For more information or to download plugins, view the <a href=\"http://chaijs.com\">documentation</a>.</p>\n<h2>What is Chai?</h2>\n<p>Chai is an <em>assertion library</em>, similar to Node's build in <code>assert</code>. It makes testing much easier by giving you lots of assertions you can run against your code.</p>\n<h2>Installation</h2>\n<h3>Node.js</h3>\n<p><code>chai</code> is available on <a href=\"http://npmjs.org\">npm</a>. To install it, type:</p>\n<pre><code>$ npm install chai\n</code></pre>\n<h3>Browsers</h3>\n<p>You can also use it within the browser; install via npm and use the <code>chai.js</code> file found within the download. For example:</p>\n<pre><code class=\"language-html\">&#x3C;script src=\"./node_modules/chai/chai.js\">&#x3C;/script>\n</code></pre>\n<h2>Usage</h2>\n<p>Import the library in your code, and then pick one of the styles you'd like to use - either <code>assert</code>, <code>expect</code> or <code>should</code>:</p>\n<pre><code class=\"language-js\">var chai = require('chai');  \nvar assert = chai.assert;    // Using Assert style\nvar expect = chai.expect;    // Using Expect style\nvar should = chai.should();  // Using Should style\n</code></pre>\n<h3>Pre-Native Modules Usage (<em>registers the chai testing style globally</em>)</h3>\n<pre><code class=\"language-js\">require('chai/register-assert');  // Using Assert style\nrequire('chai/register-expect');  // Using Expect style\nrequire('chai/register-should');  // Using Should style\n</code></pre>\n<h3>Pre-Native Modules Usage (<em>as local variables</em>)</h3>\n<pre><code class=\"language-js\">const { assert } = require('chai');  // Using Assert style\nconst { expect } = require('chai');  // Using Expect style\nconst { should } = require('chai');  // Using Should style\nshould();  // Modifies `Object.prototype`\n\nconst { expect, use } = require('chai');  // Creates local variables `expect` and `use`; useful for plugin use\n</code></pre>\n<h3>Native Modules Usage (<em>registers the chai testing style globally</em>)</h3>\n<pre><code class=\"language-js\">import 'chai/register-assert';  // Using Assert style\nimport 'chai/register-expect';  // Using Expect style\nimport 'chai/register-should';  // Using Should style\n</code></pre>\n<h3>Native Modules Usage (<em>local import only</em>)</h3>\n<pre><code class=\"language-js\">import { assert } from 'chai';  // Using Assert style\nimport { expect } from 'chai';  // Using Expect style\nimport { should } from 'chai';  // Using Should style\nshould();  // Modifies `Object.prototype`\n</code></pre>\n<h3>Usage with Mocha</h3>\n<pre><code class=\"language-bash\">mocha spec.js -r chai/register-assert  # Using Assert style\nmocha spec.js -r chai/register-expect  # Using Expect style\nmocha spec.js -r chai/register-should  # Using Should style\n</code></pre>\n<p><a href=\"http://chaijs.com/guide/styles/\">Read more about these styles in our docs</a>.</p>\n<h2>Plugins</h2>\n<p>Chai offers a robust Plugin architecture for extending Chai's assertions and interfaces.</p>\n<ul>\n<li>Need a plugin? View the <a href=\"http://chaijs.com/plugins\">official plugin list</a>.</li>\n<li>Want to build a plugin? Read the <a href=\"http://chaijs.com/guide/plugins/\">plugin api documentation</a>.</li>\n<li>\n<p>Have a plugin and want it listed? Simply add the following keywords to your package.json:</p>\n<ul>\n<li><code>chai-plugin</code></li>\n<li><code>browser</code> if your plugin works in the browser as well as Node.js</li>\n<li><code>browser-only</code> if your plugin does not work with Node.js</li>\n</ul>\n</li>\n</ul>\n<h3>Related Projects</h3>\n<ul>\n<li><a href=\"https://github.com/chaijs/chai-docs\">chaijs / chai-docs</a>: The chaijs.com website source code.</li>\n<li><a href=\"https://github.com/chaijs/assertion-error\">chaijs / assertion-error</a>: Custom <code>Error</code> constructor thrown upon an assertion failing.</li>\n<li><a href=\"https://github.com/chaijs/deep-eql\">chaijs / deep-eql</a>: Improved deep equality testing for Node.js and the browser.</li>\n<li><a href=\"https://github.com/chaijs/type-detect\">chaijs / type-detect</a>: Improved typeof detection for Node.js and the browser.</li>\n<li><a href=\"https://github.com/chaijs/check-error\">chaijs / check-error</a>: Error comparison and information related utility for Node.js and the browser.</li>\n<li><a href=\"https://github.com/chaijs/loupe\">chaijs / loupe</a>: Inspect utility for Node.js and browsers.</li>\n<li><a href=\"https://github.com/chaijs/pathval\">chaijs / pathval</a>: Object value retrieval given a string path.</li>\n<li><a href=\"https://github.com/chaijs/get-func-name\">chaijs / get-func-name</a>: Utility for getting a function's name for node and the browser.</li>\n</ul>\n<h3>Contributing</h3>\n<p>Thank you very much for considering to contribute!</p>\n<p>Please make sure you follow our <a href=\"https://github.com/chaijs/chai/blob/master/CODE_OF_CONDUCT.md\">Code Of Conduct</a> and we also strongly recommend reading our <a href=\"https://github.com/chaijs/chai/blob/master/CONTRIBUTING.md\">Contributing Guide</a>.</p>\n<p>Here are a few issues other contributors frequently ran into when opening pull requests:</p>\n<ul>\n<li>Please do not commit changes to the <code>chai.js</code> build. We do it once per release.</li>\n<li>Before pushing your commits, please make sure you <a href=\"https://github.com/chaijs/chai/blob/master/CONTRIBUTING.md#pull-requests\">rebase</a> them.</li>\n</ul>\n<h3>Contributors</h3>\n<p>Please see the full\n<a href=\"https://github.com/chaijs/chai/graphs/contributors\">Contributors Graph</a> for our\nlist of contributors.</p>\n<h3>Core Contributors</h3>\n<p>Feel free to reach out to any of the core contributors with your questions or\nconcerns. We will do our best to respond in a timely manner.</p>\n<p><a href=\"https://github.com/logicalparadox\"><img src=\"https://avatars3.githubusercontent.com/u/58988?v=3&#x26;s=50\" alt=\"Jake Luer\"></a>\n<a href=\"https://github.com/vesln\"><img src=\"https://avatars3.githubusercontent.com/u/330048?v=3&#x26;s=50\" alt=\"Veselin Todorov\"></a>\n<a href=\"https://github.com/keithamus\"><img src=\"https://avatars3.githubusercontent.com/u/118266?v=3&#x26;s=50\" alt=\"Keith Cirkel\"></a>\n<a href=\"https://github.com/lucasfcosta\"><img src=\"https://avatars3.githubusercontent.com/u/6868147?v=3&#x26;s=50\" alt=\"Lucas Fernandes da Costa\"></a>\n<a href=\"https://github.com/meeber\"><img src=\"https://avatars3.githubusercontent.com/u/17260989?v=3&#x26;s=50\" alt=\"Grant Snodgrass\"></a></p>","docPath":"argo-ci/node_modules/chai/readme","proj":"argo-ci"}}

